/* ========== START HERO STYLES ========== */
.hero-container {
  background-color: inherit;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

.photo-container {
  width: 45vw;
  height: auto;
  overflow: visible;
}

.cd-photo {
  background-image: url('./assets/images/cd-photo-hero.png');
  background-size: cover;
  background-position: top;
  animation: 1.8s ease 0s 1 zoomIn;
  -webkit-animation: 1.8s ease 0s 1 zoomIn;
  margin-left: 2rem;
  max-height: 100%;
  width: 100%;
  min-height: 50vh;
  transition: all 0.5s ease;
  /* box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3); */
}

.hero-text {
  font-family: 'Aboreto', cursive;
  color: white;
  /* letter-spacing: .2rem; */
  font-weight: 300;
  font-size: clamp(3rem, 1.6vw, 4rem);
  margin-right: 2rem;
  text-align: right;
}

.custom-card-styles {
  background-color: inherit;
  color: white;
  align-items: center;
  border: none;
  font-family: 'Oswald', sans-serif;
  font-weight: 100;
}

.card-title {
  color: rgb(158, 41, 135);
  font-weight: bold;
}

.card-subtitle {
  font-weight: 200;
}

.card-link {
  position: relative;
  text-decoration: none;
  color: white;
  padding-bottom: 5px;
}

.card-link::after {
  content: "";
  display: block;
  height: 2px;
  background-color: rgb(158, 41, 135);
  margin-top: 5px;
  /* Adjust this value to change the distance between the text and the line */
  margin-left: -5px;
  /* Adjust this value to extend the line to the left */
  margin-right: -5px;
  /* Adjust this value to extend the line to the right */
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.cd-group-photo {
  background-image: url('./assets/images/cd-group-photo.jpeg');
  background-size: cover;
  background-position: center;
  animation: 1.8s ease 0s 1 zoomIn;
  -webkit-animation: 1.8s ease 0s 1 zoomIn;
  max-height: 100%;
  /* width: 40%; */
  min-height: 50vh;
  transition: all 0.5s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

.join-convo-text,
.event-header {
  display: flex !important;
  justify-content: center;
}

.join-convo-text {
  font-weight: 600;
  color: rgb(233, 233, 233);
}

.event-header {
  font-weight: 200;
  color: rgb(233, 233, 233);

}

.event-styles {
  border: 1px solid #243E8C;
  border-radius: 10px;
  margin: 0 1rem 0 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);

}

/* ~~~Button Styles~~~ */
.btn {
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  color: rgb(255, 255, 255) !important;
}

.btn:hover .btn-slide-show-text1 {
  margin-left: 65px;
}

.btn-moving-gradient {
  height: 3rem;
  width: 10rem;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  border: none;
  background-size: 300% 100%;
  border-radius: 50px;
}

.btn-moving-gradient:hover {
  transition: all 0.5s ease-in-out 0s;
  background-position: 100% 0px;
}

.btn-moving-gradient--blue {
  background-image: linear-gradient(90deg, rgb(215 106 193), rgb(158, 41, 135), #8484f8, #4a4ab0);
  ;
  box-shadow: rgb(0, 0, 0) 0px 1px 2px 0px;
}

