/* ========== END HERO STYLES ========== */

/* ========== START ABOUT SECTION STYLES ========== */
.timeline {
  position: relative;
  margin-top: 22px;
  list-style: none;
  padding: 4px 0 0;
}

.timeline>li:nth-child(even) {
  position: relative;
  margin-bottom: 50px;
  height: 11.25rem;
  right: -5rem;
}

.timeline>li:nth-child(odd) {
  position: relative;
  margin-bottom: 50px;
  height: 11.25rem;
  left: -5rem;
}

.timeline>li:before,
.timeline>li:after {
  content: "";
  display: table;
}

.timeline>li:after {
  clear: both;
  min-height: 10.625rem;
}

.timeline>li .timeline-panel {
  position: relative;
  float: left;
  width: 41%;
  text-align: right;
  padding: 0 20px 20px 100px;
}

.timeline>li .timeline-image {
  z-index: 100;
  position: absolute;
  left: 50%;
  border: 7px solid #bf164d;
  border-radius: 100%;
  background-color: #bf164d;
  /* box-shadow: 0 0 5px #ccc; */
  width: 12.5rem;
  height: 12.5rem;
  margin-left: -100px;
}

.timeline>li .timeline-image h4 {
  margin-top: 12px;
  font-size: 10px;
  line-height: 14px;
}


.timeline>li.timeline-inverted>.timeline-panel {
  float: right;
  text-align: left;
  padding: 0 100px 20px 20px;
}

.timeline>li:last-child {
  margin-bottom: 0;
}

.timeline .timeline-heading h4 {
  margin-top: 22px;
  margin-bottom: 4px;
  color: #b3b3b3;
  padding: 0;
}

.timeline .timeline-heading h4.subheading {
  text-transform: none;
  font-size: 18px;
  color: #333;
  margin: 0;
  padding: 0;
}

.timeline .timeline-body>p,
.timeline .timeline-body>ul {
  margin-bottom: 0;
  color: gray;
}

.timeline>li:nth-child(odd) .line:before {
  content: "";
  position: absolute;
  top: 7.75rem;
  bottom: 0;
  left: 43.125rem;
  width: 4px;
  height: 11.25rem;
  background-color: #bf164d;
  -ms-transform: rotate(-44deg);
  -webkit-transform: rotate(-44deg);
  transform: rotate(-44deg);
  box-shadow: 0 0 5px #ccc;
}

.timeline>li:nth-child(even) .line:before {
  content: "";
  position: absolute;
  top: 9.75rem;
  bottom: 0;
  left: 33.125rem;
  width: 4px;
  height: 11.25rem;
  background-color: #bf164d;
  -ms-transform: rotate(44deg);
  -webkit-transform: rotate(44deg);
  transform: rotate(44deg);
  box-shadow: 0 0 5px #ccc;
}

.timeline>li .timeline-panel:before,
.timeline>li.timeline-inverted>.timeline-panel:before {
  right: auto;
  left: -15px;
  border-right-width: 15px;
  border-left-width: 0;
}

.timeline>li .timeline-panel:after,
.timeline>li.timeline-inverted>.timeline-panel:after {
  right: auto;
  left: -14px;
  border-right-width: 14px;
  border-left-width: 0;
}

/*  */
img,
svg {
  vertical-align: middle;
}

/* ========== END ABOUT SECTION STYLES ========== */

/* ========== START ISSUES STYLES ========== */
